/**
 * OCR Services using both Tesseract.js and Azure Computer Vision API
 * Provides functionality to extract text from images and PDFs
 */

import { createWorker } from 'tesseract.js';
import { DocumentAnalysisClient, AzureKeyCredential } from '@azure/ai-form-recognizer';
import { Partner } from '../types';

// Azure Computer Vision API credentials
const azureKey = '6OaBlmEj6cevfq2dPdCdWIVfrG0mVNBQ4QYvJIBMTWr7cwhN7l5wJQQJ99BCACYeBjFXJ3w3AAAFACOG73Ks';
const azureEndpoint = 'https://walshyy.cognitiveservices.azure.com/';

/**
 * Extract text using Tesseract.js
 * @param file The image or PDF file to extract text from
 * @returns A promise that resolves to the extracted text
 */
export const extractTextWithTesseract = async (file: File): Promise<string> => {
  try {
    const worker = await createWorker('eng');
    
    // Convert file to image data URL
    const fileReader = new FileReader();
    const dataUrl = await new Promise<string>((resolve, reject) => {
      fileReader.onload = () => resolve(fileReader.result as string);
      fileReader.onerror = reject;
      fileReader.readAsDataURL(file);
    });
    
    const { data: { text } } = await worker.recognize(dataUrl);
    await worker.terminate();
    
    return text;
  } catch (error) {
    console.error('Error extracting text with Tesseract:', error);
    throw new Error('Text extraction with Tesseract failed');
  }
};

/**
 * Extract text using Azure Computer Vision API
 * @param file The image or PDF file to extract text from
 * @returns A promise that resolves to the extracted text
 */
export const extractTextWithAzure = async (file: File): Promise<string> => {
  try {
    const client = new DocumentAnalysisClient(
      azureEndpoint,
      new AzureKeyCredential(azureKey)
    );
    
    // Convert file to Uint8Array
    const arrayBuffer = await file.arrayBuffer();
    const buffer = new Uint8Array(arrayBuffer);
    
    // Analyze the document
    const poller = await client.beginAnalyzeDocument('prebuilt-document', buffer);
    const result = await poller.pollUntilDone();
    
    if (!result || !result.content) {
      throw new Error('No content extracted');
    }
    
    return result.content;
  } catch (error) {
    console.error('Error extracting text with Azure:', error);
    throw new Error('Text extraction with Azure failed');
  }
};

/**
 * Parse extracted text to find partner data
 * @param text The extracted text from an OCR service
 * @returns An array of partner objects
 */
export const parsePartnerData = (text: string): Partner[] => {
  try {
    const partners: Partner[] = [];
    
    // Regular expressions to find the headings and data
    const headingPattern = /(Partner Name|Partner Number|Total Tippable Hours)/gi;
    const dataPattern = /([A-Za-z\s]+)\s+(\d+)\s+(\d+(\.\d+)?)/g;
    
    // Check if the headings exist in the text
    if (!headingPattern.test(text)) {
      throw new Error('Required headings not found in the document');
    }
    
    // Extract partner data
    let match;
    while ((match = dataPattern.exec(text)) !== null) {
      // Extracted data: name, number, hours
      const name = match[1].trim();
      const number = match[2].trim();
      const hours = parseFloat(match[3]);
      
      // Only add if we have valid data
      if (name && number && !isNaN(hours)) {
        partners.push({ name, number, hours });
      }
    }
    
    return partners;
  } catch (error) {
    console.error('Error parsing partner data:', error);
    throw new Error('Failed to parse partner data from extracted text');
  }
};

/**
 * Extract partner data from a file using OCR
 * @param file The image or PDF file to extract data from
 * @returns A promise that resolves to an array of partner objects
 */
export const extractPartnerData = async (file: File): Promise<Partner[]> => {
  try {
    // Try Azure first, fall back to Tesseract
    let text: string;
    try {
      text = await extractTextWithAzure(file);
    } catch (azureError) {
      console.warn('Azure OCR failed, falling back to Tesseract:', azureError);
      text = await extractTextWithTesseract(file);
    }
    
    return parsePartnerData(text);
  } catch (error) {
    console.error('Error extracting partner data:', error);
    throw error;
  }
}; 